@import "../../global.scss";


.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-style: 50px;
    }
    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;
    }
    .container{
        width: 60%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;

        .item{
            width: 250px;
            height: 200px;
            border-radius: 20px;
            border: 1px solid lightgray;
            margin: 20px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;       
            transition: all 0.5s ease;     
            cursor: pointer;
            img{
                width: 100%;
                height: 100%;
                object-fit: fill;
                z-index: 1;
            }
            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
            h3{
                position: absolute;
                font-style: 20px;
                text-align: center;
                padding: 20px;
            }
        }
    }
}